import React, { Component } from 'react';
import { HashLink as HLink } from 'react-router-hash-link';

class Navbar extends Component {

    constructor(props) {
        super(props);
        this.state = { addClass: 'Say hello' }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll.bind(this));

    }
    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll.bind(this));
    }
    handleScroll() {
        let lastScrollY = window.scrollY

        if (lastScrollY >= 60) {

            this.setState((state, props) => ({
                addClass: 'nav-sticky'
            }));

        } else {

            this.setState((state, props) => ({
                addClass: ''
            }));

        }

    }
    render() {

        let logoAlt = "FunBrain";

        return (
            <div>
                <nav className={`${this.state.addClass} navbar navbar-inverse navbar-expand-lg header-nav fixed-top header`}>
                    <div className="container">
                        <a className="navbar-brand logo" href={`${process.env.PUBLIC_URL}/`}>
                            <img className="logo-dark" src={process.env.PUBLIC_URL + '/assets/img/logo.png'} alt={logoAlt} width={"auto"} height={70} />
                            <img className="logo-light" src={process.env.PUBLIC_URL + '/assets/img/logo.png'} alt={logoAlt} width={"auto"} height={70} />
                        </a>
                        <button className="navbar-toggler pull-right" type="button" data-toggle="collapse" data-target="#navbarCodeply">
                            <i className="icofont-navigation-menu"></i>
                        </button>

                        <div className="collapse navbar-collapse" id="navbarCodeply">
                            <ul className="nav navbar-nav ml-auto">
                                <li><HLink className="nav-link" smooth to={`/#home`}></HLink></li>
                                <li><HLink className="nav-link" smooth to={`/#features`}></HLink></li>
                                <li><HLink className="nav-link" smooth to={`/#special-features`}></HLink></li>
                                <li><a href="mailto:zlcsc21@gmail.com" className="nav-link"></a></li>
                                <li><a href="https://forms.gle/omGQVRRGb5LHmt9Z7" className="nav-link"></a></li>
                            </ul>
                        </div>

                    </div>
                </nav>
            </div>
        )
    }
}


export default Navbar