import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, HashRouter, Route, Switch } from "react-router-dom";
import Navbar from './components/global-components/navbar';
import Home from './components/home';
import Fof from './components/global-components/fof';
import Footer from './components/global-components/footer';
import FofData from './data/fof';
import ScrollToTop from './components/global-components/test';

class Root extends Component {
    render() {
        return(
            <Router>
                <HashRouter basename="/">
                <div>
                <Navbar />
                <ScrollToTop />
                <Switch>
                    <Route exact path="/" component={Home} />
                    <Route path="/fof" render = { () => { return <Fof data={FofData} /> } } />
                    <Route render = { () => { return <Fof data={FofData} /> } } />
                </Switch>
                <Footer />
                </div>
                </HashRouter>
            </Router>
        )
    }
}

export default Root;

ReactDOM.render(<Root />, document.getElementById('zlcodecamp'));
